	module.exports = {"data":{"project":{"html":"<p>I led development of <a href=\"http://atlas.qz.com\">Atlas</a>, Quartz's <a href=\"http://qz.com/434425/atlas-the-new-home-for-charts-and-data/\">platform for discovering and sharing charts</a>.</p>\n<p>The site is built with React and Node.js and is <a href=\"http://www.smashingmagazine.com/2015/04/21/react-to-the-future-with-isomorphic-apps\">isomorphic</a>. Quite the buzzword, but it means that the same React javascript components are rendered on the server-side as well as the client-side, which proved to be a huge convenience in terms of development. In the past, I've worked on projects that either forego server-side rendering of content completely (and rely solely on the API) and others that use two separate templates to render the same exact markup (PHP and underscore.js, for example). I introduced the team to Node.js and React + Flux while developing the site.</p>","frontmatter":{"title":"Atlas","link":"http://atlas.qz.com","tags":null,"github":null,"image":{"childImageSharp":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":1.7779960707269156,"src":"/static/atlas-featured-f47f77a68b2c24ab504df5ab564ee450-68fcc.png","srcSet":"/static/atlas-featured-f47f77a68b2c24ab504df5ab564ee450-568b0.png 198w,\n/static/atlas-featured-f47f77a68b2c24ab504df5ab564ee450-32057.png 395w,\n/static/atlas-featured-f47f77a68b2c24ab504df5ab564ee450-68fcc.png 790w,\n/static/atlas-featured-f47f77a68b2c24ab504df5ab564ee450-9601e.png 1185w,\n/static/atlas-featured-f47f77a68b2c24ab504df5ab564ee450-796a2.png 1580w,\n/static/atlas-featured-f47f77a68b2c24ab504df5ab564ee450-ac5e7.png 1810w","sizes":"(max-width: 790px) 100vw, 790px"}}},"created":"June 2015"}}},"pathContext":{"slug":"/projects/atlas/"}}

